body{
    background-color: black;
    color: white;
    margin-top: 50px;
    margin-bottom: 50px;
    margin-left: 10%;
    margin-right: 10%;
    font-family: sans-serif;
}

.hidden {
    display: none;
}
.flexbox{
    display: flex;
    flex-wrap: wrap;
}

.pokemon-font {
    font-family: "Press Start 2P";
}

img {
    image-rendering: crisp-edges;
}

.hero {
    text-align: center;
}
.atp-logo {
    width: 50px;
}

.search {
    align-items: start;
    margin-top: 100px;
    height: min-content;
}
.search .flexbox {
    justify-content: center;
    gap: 10px;
}
.type-buttons{
    margin-top: 100px;
    gap: 20px;
    width: 250px;
    margin-left: 50%;
    transform: translate(-50%, -50%);
}
.type-button:hover{
    cursor: pointer;
    opacity: 50%;
}

.search h4 {
    margin: 0;
    margin-top: 1px;
}
.searchBar {
    width: 200px;
    height: min-content;
    margin-left: 10px;
    margin-right: 10px;
    align-content: center;
}

.loading {
    position: fixed;
    margin: auto;
    left: 50%;
    top: 75%;
    transform: translate(-50%, -50%);
    z-index: 100;
}

.loading h5{
    text-align: center;
    margin-top: 0;
}
.loading img {
    width: 100px;
    display: block;
    margin-left: auto;
    margin-right: auto;
    margin-top: 100px;
}

.pokemon-list{
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 50px;
    margin-top: 50px;
}

.card{
    background-color: rgb(19, 19, 19);
    width: 150px;
    border-radius: 5px;
    position: relative;
    text-align: center;
}
.card:hover {
    transform: scale(110%);
    transition: 200ms;
    cursor: pointer;
}
.card-title {
    text-transform: capitalize;
    margin: 0;
    margin-top: 30px;
    font-size: 13px;
}
.num {
    position: absolute;
    margin: 0px;
    top: 10px;
    left: 10px;
    font-size: 10px;
}
.card-img {
    text-align: center;
    margin: 10px;
}

.modal {
    position: fixed;
    background-color: rgb(36, 36, 36);
    width: 700px;
    height: 600px;
    border-radius: 5px;
    text-align: center;
    margin: auto;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
}
.closer {
    position: absolute;
    top: 10px;
    right: 10px;
    width: 20px;
}
.closer:hover {
    opacity: 50%;
    transition: 200ms;
    cursor: pointer;
}
.modal-header {
    border-bottom: solid rgb(19, 19, 19);
    align-items: center;
    justify-content: space-between;
    padding-left: 50px;
    padding-right: 50px;
    padding-top: 10px;
    padding-bottom: 10px;
    gap: 0;
}
.modal-img {
    width: 200px;
    background-color: rgb(19, 19, 19);
    border-radius: 5px;
}

.modal-title-text {
    font-size: 35px;
    text-transform: capitalize;
    text-align: right;
}
.modal-description h2, .modal-description p{
    text-align: left;
    margin-left: 50px;
    font-weight: bolder;
}
#types, #abilities {
    align-items: center;
}
.type {
    height: 20px;
    margin-left: 20px;
}
.ability {
    margin-left: 20px;
    text-transform: capitalize;
    color: rgb(169, 255, 169)
}
.stats {
    margin-top: 15px;
    font-size: 10px;
}
.stat-bars {
    margin-top: 15px;
}
.stat-bar {
    background-color: aqua;
    margin-top: 11.6px;
    margin-left: 20px;
    height: 10px;
}
.attr {
    position: absolute;
    left: 200px;
}
.height, .weight {
    color: rgb(255, 148, 148)
}


@media (max-width: 730px){
    body{
        margin-left: 0;
        margin-right: 0;
    }
    .modal {   
        width: 350px;
        height: 410px;
    }
    
    .modal-header{
        padding-left: 25px;
        padding-right: 25px;
    }
    
    .modal-img {
        width: 100px;
    }
    
    .modal-title-text {
        font-size: 18px;
    }
    
    .modal-description h2, .modal-description p{
        margin-left: 12px;
    }
    .modal-description h2 {
        font-size: 15px;
    }

    .type {
        height: 20px;
        margin-left: 10px;
    }
    
    
    .ability {
        margin-left: 10px;
        font-size: 12px;
    }
    
    .stats {
        margin-top: 8px;
        font-size: 10px;
    }
    
    .stat-bars {
        margin-top: 8px;
    }
    
    .stat-bar {
        background-color: aqua;
        margin-top: 11.6px;
        margin-left: 5px;
        height: 10px;
    }
    
    .attr {
        position: absolute;
        left: 120px;
    }
    
    #attributes h2{
        font-size: 15px;
    }
    .pokemon-list{
        gap: 0px;
    }
    .card {
        transform: scale(75%);
    }
    .card:hover {
        transform: scale(80%);
        transition: 200ms;
        cursor: pointer;
    }
}